.Contact
{
    height : 70vh;
    border : 1px solid white;
    position : relative;
}
.get
{
    margin-top : 20vh;
    color:black;
    font-size: 13vh;
    font-family: 'Ubuntu Condensed', sans-serif;
    /* border : 1px solid red; */
}

.accountsLink
{
    margin-top : 10vh;
    /* border : 1px solid black; */
    display : flex;
    align-items: center;
    justify-content: center;
}

.accountsLink div
{

    margin : 1vw;
    /* border : 1px solid red; */
}

.instagram
{
    height : 11vh;
    width: 11vh;;
    background-image : url(https://assets.stickpng.com/images/580b57fcd9996e24bc43c521.png);
    background-size:cover ;
    cursor: pointer;
    /* border : 1px solid red; */
}

.codechef {
    background-image: url(https://i.pinimg.com/originals/c5/d9/fc/c5d9fc1e18bcf039f464c2ab6cfb3eb6.jpg) ;
    background-size: cover;
    border-radius : 1vh;
    padding : 0.5vh;
    height:11vh;
    width: 11vh;
    cursor: pointer;
    /* border: 1px solid red; */
}

.codeforces {
    background-image: url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQ0FR-j-zhkGQUzVex4IbloRFlR1rYS5dkYsA&usqp=CAU);
    height: 11vh;
        width: 11vh;
    background-size: cover;
    cursor: pointer;
}

.linkedin{
    background-image: url(https://cdn-icons-png.flaticon.com/512/174/174857.png);
    /* border : 1px solid white; */
    height: 10vh;
        width: 10vh;
    background-size: cover;
    cursor: pointer;
}


@media screen and (max-device-width: 480px) {
 .get{
    font-size: 8vh;
    text-align: center;
    margin-top : 30vh;
 }   

.instagram {
    height: 9vh;
    width: 9vh;
}

.codechef {
    height: 9.5vh;
    width: 9.5vh;
}

.codeforces {
    height: 9.3vh;
    width: 9.3vh;
}

.linkedin {
    height: 9vh;
    width: 9vh;
}
}
