#mongodb
{
    height : 16vh;
    /* border : 1px solid gray; */
}

#reactjs {
    height: 15vh;
    /* border: 1px solid gray; */
    transition: all;
    animation: rotate 8s infinite;
    animation-timing-function: linear;
}

@keyframes rotate
{
    100%
    {
        transform: rotate(360deg);
    }
}

#nodejs {
    height: 16vh;
    /* border: 1px solid gray; */
    /* box-shadow : 0px 6px 6px 5px rgb(222, 220, 220); */
}

#express
{
    width : 15vh;
}

#mongodbdiv {
    /* height: 15vh; */
    position : relative;
    /* top : -40px; */
    /* border: 1px solid gray; */
}

#reactjsdiv {
    position : relative;
    /* height: 15vh; */
    /* border: 1px solid gray; */
}

#nodejsdiv {
    /* height: 17vh; */
    position: relative;
    /* border: 1px solid gray; */
}

#expressdiv {
    /* width: 10vw; */
    position: relative;
    /* border: 1px solid gray; */
}

#images div
{
    animation: bounce 0.5s infinite alternate;
}

@keyframes bounce 
{
    100%
    {
        top: -30px;
    }

    0%
    {
        top : 30px;
    }
}

#images div:nth-child(2)
{
    animation-delay : 0.1s;
}
#images div:nth-child(3) {
    animation-delay: 0.2s;
}
#images div:nth-child(4) {
    animation-delay: 0.3s;
}




