.Navbar
{
    display: flex;
    position : fixed;
    top : 0;
    width : 100vw;
    flex-direction: row;
    justify-content: space-between;
    /* border : 1px solid red; */
    margin-top: 0.5vh;
    z-index: 5;
}

.logo
{
    margin-left:10vh;
    color : rgb(5, 161, 239);
    opacity: 0;
    font-size : 5vh;
    display : flex;
    align-items: center;
    justify-content: center;
    font-weight: xx-large;
    font-family: 'Ubuntu Condensed', sans-serif;
    text-shadow : 5px 5px 2px 3px rgb(187, 1, 1 , 0.9);
    cursor: pointer;
    text-align: center;
}

.other
{
    display: flex;
    cursor: pointer;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    margin-right: 4vh;
    padding-bottom: 0vh;
}

.functionalities
{
    /* font-size: large; */
    padding : 0.1vw;
    text-shadow: 1px 1px white;
    margin: 0 1vh;
}

.functionalities:hover
{
    color : rgb(3, 192, 255);
    /* font-weight: bold; */
    text-shadow: 1px 1px 2px 3px gray;
}

#name
{
    text-shadow: 1px 1px rgb(3, 74, 109);;
}



@media screen and (max-device-width: 480px) {

    .Navbar
    {
        flex-direction: column;
        /* border : 1px solid red; */
        margin-left : 0vh;
        width: 100vw;
    }

    .logo {
        margin-left: 0vh;
        font-size: xx-large;
    }
    .other
    {
        margin-top : 4vh;
        margin-right: 0vh;
        /* border : 1px solid black; */
    }
    .other div{
        margin : 0.5vh;
    }
    .functionalities {
        font-size: 6vw;
    }
}

a
{
    text-decoration: none;
    color : black;
}

.activated
{
    border-bottom : 2px solid black;
}