#model
{
    position: fixed;
    z-index: 0;
    width : 30vw;
    top : 50%;
    transform: translate(0 , -50%);
    object-fit: cover;
    z-index: -1;
    /* border : 1px solid black; */
    opacity: 0.05;
}

.intro
{
    display : flex;
    position : fixed;
    /* border : 1px solid red; */
    flex-direction: column;
    width: 30%;
    height : 100vh;
    left : 20%;
    align-items: center;
    justify-content: center;
    z-index: 6;
}

.introFirst
{
    font-family: 'Ubuntu Condensed', sans-serif;
    font-size : xxx-large;
    width: 100%;
    text-align: left;
    position : relative;
    /* left : 500px; */
}

.introName
{
    font-family: 'Ubuntu Condensed', sans-serif;
    font-size: xxx-large;
    width: 100%;
    position: relative;
    /* left: 500px; */
    /* border : 1px solid blue; */
    text-align: left;
    color: rgb(16, 175, 255);
}

.aboutText
{
    position: relative;
    z-index: 4;
    /* border : 1px solid black; */
}


.faltu
{
    position: relative;
    top : 100vh;
    height : 100vh;
    /* border : 1px solid red; */
}

.secondfaltu {
    position: relative;
    top: 100vh;
    height: 100vh;
    /* border: 4px solid greenyellow; */
    display : flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(5, 5, 5, 0.9);
}
.secondfaltu div
{
    font-family: 'Ubuntu Condensed', sans-serif;
    margin : 0.5vw;
}
.secondfaltu div span
{
    font-weight: bold;
}

.secondfaltu div
{
    position: relative;
    z-index: 0;
}
.aboutMe
{
    font-size: xxx-large;
    color: rgb(16, 175, 255);
    /* color : white; */
    position: fixed;
    z-index: 10;
    top : 30vh;
    left : 50%;
    transform: translate(-50% , 0);
    opacity: 0;
}

.college
{
    position: fixed;
    font-size: xx-large;
    top : 40vh;
    left: 50%;
    opacity: 0;
    transform: translate(-50%, 0);
    color : white;
}

.CodeChef
{
    font-size: xx-large;
    position : fixed;
    opacity: 0;
    top: 50vh;
    left: 50%;
    transform: translate(-50%, 0);
    color: white;
}

.CodeForces {
    font-size: xx-large;
    left: 150%;
    opacity: 0;
    position: fixed;
    top: 60vh;
    left: 50%;
    transform: translate(-50%, 0);
    color: white;
}
.CodeForces span
{
    color : rgb(68, 182, 248);
}
.WebDeveloper{
    font-size: xx-large;
    left: -100%;
    position : fixed;
    opacity: 0;
    top: 70vh;
    left: 50%;
    transform: translate(-50%, 0);
    color: white;
}

.adjustment
{
    left : 500px;
    /* transform : translate( -50% , 0 ); */
    color : blueviolet;
}


.Footer
{
    position: fixed;
}


@media screen and (max-device-width: 480px) {
    .intro {
        width: 45%;
        height: 100vh;
        left: 5%;
        /* border : 1px solid red; */
    }

    .introFirst {
        font-family: 'Ubuntu Condensed', sans-serif;
        font-size: xx-large;
    }

    .introName {
        font-size: xx-large;
        width: 100%;
    }

#model {
    z-index: 0;
    width: 45vw;
    top: 50%;
    opacity: 0.1;
}

.faltu {
    position: relative;
    top: 100vh;
    height: 80vh;
    /* border : 1px solid red; */
}

.aboutMe {
    font-size: xx-large;
    position : fixed;
    top : 25vh;
    /* border : 1px solid red; */
}

.college {
    font-size: x-large;
    width : 70%;
    top: 34vh;
    position: fixed;
    /* left: 150vw; */;
}

.CodeChef {
    font-size: x-large;
    width: 70%;
    top: 45vh;
    left: 50%;
}

.CodeForces {
    font-size: x-large;
    top: 52vh;
    width: 70%;
    left: 50%;
}

.WebDeveloper {
    font-size: x-large;
    width: 70%;
    top: 59vh;
    left: 50%;
}

}